/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */
.wp-block-user-tags-user-directory {
  background: #fff; }

/**
Form Controls
 */
.user-directory-controls {
  background: #F5F5F5;
  padding: 2rem 1rem; }
  .user-directory-controls fieldset {
    border: none;
    display: flex;
    flex-wrap: wrap;
    padding: 0; }
    @media (min-width: 992px) {
      .user-directory-controls fieldset {
        flex-wrap: nowrap; } }

.user-directory-control {
  align-items: center;
  column-gap: 1rem;
  display: flex;
  flex-basis: 100%;
  line-height: 1;
  padding: 1.25rem 1rem; }
  @media (min-width: 992px) {
    .user-directory-control {
      flex-basis: initial; } }
  .user-directory-control > .user-directory-field {
    border: none !important;
    color: #231F20;
    font-size: 1rem;
    line-height: 1.5;
    padding: 0.5rem 1rem;
    width: 200px; }
    .user-directory-control > .user-directory-field::placeholder {
      color: #231F20;
      opacity: 1; }
    .user-directory-control > .user-directory-field:focus, .user-directory-control > .user-directory-field:focus-visible {
      outline: none; }

/**
Content Part
 */
.user-directory-content {
  background: white;
  padding: 0; }
  .user-directory-content .directory-entry {
    border-bottom: 1px solid #E1DED8;
    display: flex;
    justify-content: space-between;
    padding: 24px 0; }
  .user-directory-content .directory-entry__content {
    column-gap: 24px;
    display: flex;
    flex-wrap: wrap; }
  .user-directory-content .user-directory-field-user_title {
    align-items: flex-start;
    flex-direction: column;
    column-gap: 8px;
    display: flex;
    flex-basis: 100%;
    flex-wrap: wrap;
    word-spacing: 0.15em;
    font-size: 19px;
    font-weight: 700;
    line-height: 1.4; }
    .user-directory-content .user-directory-field-user_title a {
      color: #231F20;
      text-decoration: none; }
      .user-directory-content .user-directory-field-user_title a:focus, .user-directory-content .user-directory-field-user_title a:hover {
        color: maroon; }

.directory-entry__thumb {
  border-radius: 50%;
  border: none;
  display: block;
  flex-shrink: 0;
  height: 80px;
  overflow: hidden;
  width: 80px; }
  .directory-entry__thumb img {
    height: 100%;
    object-fit: cover;
    object-position: top;
    width: 100%; }

.user-directory-field-user_email a {
  font-size: 14px;
  font-weight: 400;
  flex-basis: 100%;
  line-height: 1.5;
  text-decoration: inherit; }

.user-directory-field-user_email i {
  color: maroon;
  transition: ease-out .2s; }

.user-directory-field-user_email:hover a,
.user-directory-field-user_email:hover i {
  color: gold; }

.user-directory-no-results-info {
  display: none;
  padding: 8px;
  font-weight: 700; }
  .user-directory-content--no-results .user-directory-no-results-info {
    display: block; }

.user-directory-no-results-info-text {
  text-align: center; }

.user-directory-content-entry--thumb-left .directory-entry {
  column-gap: 40px;
  justify-content: flex-start; }
