 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-user-tags-user-directory {
	background: #fff;
}

/**
Form Controls
 */
.user-directory-controls {
	background: #F5F5F5;
	padding: 2rem 1rem;

	fieldset {
		border: none;
		display: flex;
		flex-wrap: wrap;
		padding: 0;

		@media (min-width: 992px) {
			flex-wrap: nowrap;
		}
	}
}

.user-directory-control {
	align-items: center;
	column-gap: 1rem;
	display: flex;
	flex-basis: 100%;
	line-height: 1;
	padding: 1.25rem 1rem;

	@media (min-width: 992px) {
		flex-basis: initial;
	}

	> .user-directory-field {
		border: none !important;
		color: #231F20;
		font-size: 1rem;
		line-height: 1.5;
		padding: 0.5rem 1rem;
		width: 200px;

		&::placeholder {
			color: #231F20;
			opacity: 1;
		}

		&:focus,
		&:focus-visible {
			outline: none;
		}
	}
}

/**
Content Part
 */
.user-directory-content {
	background: white;
	padding: 0;

	//	@include media-breakpoint-up(md) {
	//		padding: 80px 112px;
	//	}

	.directory-entry {
		border-bottom: 1px solid #E1DED8;
		display: flex;
		justify-content: space-between;
		padding: 24px 0;
	}

	.directory-entry__content {
		column-gap: 24px;
		display: flex;
		flex-wrap: wrap;
	}

	.user-directory-field-user_title {
		align-items: flex-start;
		flex-direction: column;
		column-gap: 8px;
		display: flex;
		flex-basis: 100%;
		flex-wrap: wrap;
		word-spacing: 0.15em;
		font-size: 19px;
		font-weight: 700;
		line-height: 1.4;

		a {
			color: #231F20;
			text-decoration: none;

			&:focus,
			&:hover {
				color: maroon;
			}
		}
	}
}

.directory-entry__thumb {
	border-radius: 50%;
	border: none;
	display: block;
	flex-shrink: 0;
	height: 80px;
	overflow: hidden;
	width: 80px;

	img {
		height: 100%;
		object-fit: cover;
		object-position: top;
		width: 100%;
	}
}

.user-directory-field-user_email {

	a {
		font-size: 14px;
		font-weight: 400;
		flex-basis: 100%;
		line-height: 1.5;
		text-decoration: inherit;
	}

	i {
		color: maroon;
		transition: ease-out .2s;
	}

	&:hover {
		a,
		i {
			color: gold;
		}
	}
}

.user-directory-no-results-info {
	display: none;
	padding: 8px;
	font-weight: 700;

	.user-directory-content--no-results & {
		display: block;
	}
}

.user-directory-no-results-info-text {
	text-align: center;
}

.user-directory-content-entry--thumb-left {
	.directory-entry {
		column-gap: 40px;
		justify-content: flex-start;
	}
}
